<template>
	<v-container fluid class="principal" style="padding-top: 70px !important;">
		<br>
		<v-layout row>
		    <v-flex xs12>
		      <v-card class="card">
		       <v-toolbar color="cyan" dark class="navbar-fixed-propia">
		          <v-btn icon  v-on:click="go('/idespanol')" large>
		          	<v-icon class="goback" color="white"  large light>keyboard_arrow_left</v-icon>
		          </v-btn>
		          <v-toolbar-title class="title">Geografía | Hidrovía - Limites y fronteras</v-toolbar-title>
		          <v-spacer></v-spacer>
		        </v-toolbar>
	        	<center><h1 class="flat display-1">Hidrovía</h1></center>
	        	<p style="padding: 20px !important;font-size: 20px !important;">
					La cuenca del Rio de la plata involucra a los siguientes paises: Argentina, Brasil, Paraguay y Uruguay. Posee una superficie de 3.1 millones de km2, una población de 100 millones de habitantes y es considerada una región más urbanizada de América latina.  Sus rios más importantes son el Paraná, Paraguay y el Rio Uruguay... Estos Rios son vias fluviales excelentes para la navegación, historicamente han servido para el transporte de mercaderia dentro y fuera de la región.En la actualidad es posible navegar unos 2000km por el Rió paraná desde San Pablo hasta cuidad del Este,unos 1300 km por el Río Paraguay (desde el Río Apal hasta caceres) y unos 500km por el Río Uruguay (desde el Río de la Plata hasta la cuidad de Salto)
					El proyecto de Hidrovia Paraná - Paraguay es una mega obra de ingenieria   para aumentar la capacidad de tráfico de productos, principalmente agricolas(Soja), minerales(hierro) y combustibles. Dicho sistema abarca unos 3440km continuos entre el puerto de cáceres(Norte de brasil) y el puerto de nueva palmira en colonia (Uruguay)
					El proyecto está valorado en 1 billón de dólares, Debe garantizar la navegación diurna y nocturna  y durante todo el año de barcos formados por varias bolsas y un empujador. Estos barcos llegan a dimensiones de hasta 200m de largo, 16 de ancho y  3.5 de calado
					Evidentemente las condiciones naturales de los rios no permiten la navegación de elementos tan grandes para garantizar los objetivos de navegación, es necesario realizar grandes obras de ingeniería en los ecosistemas fluviales, entre lo que se incluyan enderezamiento y ensanchamiento de los cauces, Dragando permantemente, dinamitar afloramientos rocosos
	        	</p>
	        	<v-divider></v-divider>
	        	<center><h1 class="flat display-1">Limites y Fronteras</h1></center>
	        	<p style="padding: 20px !important;font-size: 20px !important;">
					<span class="flat" style="font-size: 25px !important; font-weight: bolder !important;">Limites</span>:De limitación natural o artificial que separa dos o más territorios
						<ul style="padding-left: 30px !important;">
							<li>Naturales:  (RIOS,RELIEVES,LAGOS)</li>
							<li>Artificiales: (Calles,Avenidas,Mojones, Muros)</li>
							<li>Limites Contestados: Desacuerdo por parte de dos estados en cuanto a una porción de territorio ejemplos: (Isla Brasilera)(Rincón de Artigas)</li>
						</ul>
						<br>
						<center>
							<visorimages :images="images"></visorimages>
						</center>
						<br>
						<span class="flat" style="font-size: 25px !important; font-weight: bolder !important;">Fronteras</span>: Las areas fronterizas son zonas mucho más amplias que los limites. En estas regiones se producen conflictos pero también integración cultural, deportivas y comerciales.
						En estas areas se refleja el diario convivir de personas que se relacionan, con diferencias notables de idioma, laborales, familiares. Ejemplos de cuidades fronterizas: (Rio Branco, Yaguarón)

	        	</p>
		      </v-card>
		    </v-flex>
		</v-layout>
	</v-container>
</template>
<script>
