			return{
				images:[
					{src:'http://www.radiohc.cu/uploads/images/articulos/8995-brasil-bolivia.jpg',alt:'areas fronterizas',preview:{
							width: '200px',
							height: '125px'
						}},
					{src:'https://upload.wikimedia.org/wikipedia/commons/0/0e/Uruguay_mapa.png',alt:'',preview:{
							width: '200px',
							height: '125px'
						}},
				],
			}
				
		},
		methods:{
			go(url)
			{
				this.$router.push(url);
			},
		},
		computed:{

		}
	}
</script>

