				  				<v-divider></v-divider>
				  				<span class="headline">Componentes</span>
				  				<br>
				  				<span>El sistema de componentes de vuejs 2 es una de las partes más destacables de este framework ya que por medio de componentes se puede encapsular y hacer reutilizable código de forma sencilla y limpia sin las complicaciones tipicas de intentar hacer esto con javascript vanilla. Crear un componente es tán fácil como esto: <code>Vue.component('tag',{options}); las options permitidas son <code>data(){return{//Esta vez en forma de método en breves explico el por qué de este cambio}} , methods:{}, props:[], template:''</code></code> ahora ya no utilizaremos un objeto data para poner dentro las propiedades, ahora será un método ¿por qué? pues por que si ponemos un objeto podría generar conflicto con la instancia principal de vue y es mejor evitarlo por lo tanto transformamos data en un método y de el retornamos un objeto con todas las propiedades que allí se deseen agregar.</span>
