<template>
	<div class="principal-div-fredomofexpression">
		<v-layout row >
			    <v-flex xs12 text-xs-center>
			      <v-card class="card">
			        <v-toolbar color="primary" dark class="navbar-normal-my">
			          <v-btn icon  v-on:click="go('/civica')">
			          	<v-icon class="goback" color="white" large light>keyboard_arrow_left</v-icon>
			          </v-btn>
			          <v-toolbar-title class="title">Cívica | Libertad de expresión</v-toolbar-title>
			          <v-spacer></v-spacer>
			          <v-btn icon v-on:click="teamDialog.active = !teamDialog.active">
			          	<v-icon>info</v-icon>
			          </v-btn>
			        </v-toolbar>
			      </v-card>
				 <v-jumbotron color="primary" dark class="presentacion-jumbotron">
				    <v-container fill-height>
				      <v-layout align-center>
				        <v-flex>
						  <img class="rotateEffect" width="150px" src="../../assets/derechos-logo.png" alt="">
				          <h3 class="display-3">Libertad de expresión</h3>
				          <span class="subheading">La libertad de expresión es un derecho básico y de gran importancia, ya que la democracía no estaría presente de no existir el mismo, a continuación daré ejemplos y argumentos para transmitir el mensaje de que este derecho es super necesario.</span>
				          <v-divider class="my-3"></v-divider>
				          <div class="title mb-3">Lautaro Umpierrez <br> Sofía Segovia <br> Catalina Fernández.</div>
				          <v-btn large color="red" class="mx-0" v-on:click="content.active=true">Abrir Presentación.</v-btn>
				        </v-flex>
				      </v-layout>
				    </v-container>
				  </v-jumbotron>
	        </v-flex>
	        <v-dialog class="teamDialog" v-model="teamDialog.active" persistent max-width="400">
		      <v-card>
		        <v-card-title class="headline">Work Team</v-card-title>
		        <v-card-text>
		        	<v-list>
		        		 <template v-for="(item, index) in teamDialog.people" class="item">
				            <v-list-tile avatar :key="item.name" @click="">
				              <v-list-tile-content>
				                <v-list-tile-title v-html="item.name"></v-list-tile-title>
				              </v-list-tile-content>
				            </v-list-tile>
				          </template>
		        	</v-list>
		        </v-card-text>
		        <v-card-actions>
		          <v-spacer></v-spacer>
		          <center>
		          	<v-btn color="green darken-1" flat @click.native="teamDialog.active = false">Cerrar</v-btn>
		          </center>
		        </v-card-actions>
		      </v-card>
		    </v-dialog>
		    <v-dialog v-model="content.active" persistent max-width="98%" style="padding-top: 10px !important; padding: 0px !important;overflow-y: hidden !important;">
		        <v-card>
		          <v-card-title>
		             <span class="headline">Derecho - Libertad de expresión</span>
		          </v-card-title>
		          <v-divider></v-divider>
		          <v-card-text>
						<blockquote>
						<span class="headline">¿Que es la libertad de expresión?</span>
						<br>
						<span class="subheading">
							La libertad de expresión es el derecho a dar tu punto de vista sobre cualquier tema siempre y cuando no se difame a ningúna organización y/o persona.
						</span>
					</blockquote>	
					<br>	
					<v-divider></v-divider>
					<br>
					<blockquote>
						<span class="headline">
							Ocasiones en los que no se respeta el derecho a la libre expresión:
						</span>
						<br>
						<span class="subheading">
							- Castigos por Hablar y/o opinar sobre un tema en especifico
							<br>
							- Dictadura
						</span>

					</blockquote>
					<br>
					<v-divider></v-divider>
					<br>
					<blockquote>
						<span class="headline">
							¿Por qué es tán importante el derecho a la libertad de expresión?
						</span>
						<br>
						<span class="subheading">Realmente es muy importante por el simple hecho que gracias a esta, tenemos la oportunidad de acusar injusticias y/o delitos, para que estos dejen de ser cometidos y así la vida en sociedad sea posible.</span>
					</blockquote>
					<br>
					<v-divider></v-divider>
					<br>
					<blockquote>
						<span class="headline">Conclusión:</span>
						<br>
						<span class="subheading">
							La libertad de expresión es un derecho universal, esto quiere decir que es otorgado a cualquier ser humano, es un derecho necesario y significa poder ser quien somos y tener una opinión propia, no necesariamente una colectiva.
							Nadie tiene derecho a decirte como opinar, todos tenemos derecho a tener nuestros ideales y a sociedad debe respetarlos siempre y cuando no incumpla ningún otro derecho universal,
							Yo entiendo que un derecho tiene cierto alcanze, ese alcanze es hasta que comienze a hacer conflicto con otro derecho, un ejemplo de esto es que "Tu tienes libertad de expresión, pero si tu tienes una opinión discriminatoria hacia los homosexuales, personas de color o cualquier otro colectivo" ya comienzas a incumplir y ofuscar un derecho de otra persona, por lo cual siempre hay que tener en claro los limites, y saber que un derecho termina donde otro comienza.
						</span>

						<center>
							<visorimages :images="images"></visorimages>
						</center>
					</blockquote>
		          </v-card-text>
		          <v-card-actions class="card-action-my-my">
		            <v-btn color="primary" flat @click.stop="content.active=false">Cerrar</v-btn>
		          </v-card-actions>
		        </v-card>
		      </v-dialog>
	    </v-layout>
	</div>
</template>
<script>
	import visorimages from './../reutilizable/Visorimagenes.vue';
	export default{
		components:{visorimages},
		data(){
			return{
				content:{
					active:false
				},
				teamDialog:{
					active:false,
					people:[
						{name:'Lautaro Umpierrez'},
						{name:'Sofía Segovia'},
						{name:'Catalina Fernández'}
					]
				},
				images:[
					{src:'http://libertadbajopalabra.com/wp-content/uploads/2017/06/lde-690x463.jpg',alt:'Libertad de expresión',preview:{
							width: '200px',
							height: '125px'
						}},
					{src:'http://www.solo-clic.com/wp-content/uploads/2013/12/0010421805.jpg',alt:'Cuba libertad de expresión.',preview:{
							width: '200px',
							height: '125px'
						}},
					
				],
			}
		},
		methods:{
			go(url)
			{
				this.$router.push(url);
			},
		},
		computed:{

		}
	}
</script>
<style lang="scss">
	.black--text-my
	{
		color: #333333 !important;
	}
	.presentacion-jumbotron
	{
		position: absolute !important;
		top: 50px !important;
		height: calc(100% - 25px) !important;
	}
	html{
		background-color: #1976d2 !important;
	}
	.principal-div-fredomofexpression
	{
		overflow: hidden !important;
		background-color: #1976d2 !important;
	}
	.navbar-normal-my
	{
		margin-top: -1px !important;
	}
	h1.flat,h2.flat,h3.flat,h4.flat,h5.flat,h6.flat
	{
		font-weight: 300 !important;
	}
	.media 
	{
		padding: 0 !important;
		.card__media__background{
			width: 50% !important;

			margin-left: 50% !important;
		}
	}
	@media screen and (max-width: 768px)
	{
		.media 
		{
			.card__media__background{
				width: 100% !important;
				margin-left: 0% !important;
			}
		}
	}
	.padding-left-5
	{
		padding-left: 5px !important;
	}
	.resalted
	{
		font-style: italic !important;
	}
	.teamDialog .dialog
	{
		background-color: #fff !important;
	}
	.teamDialog .item
	{
		border-bottom: 1px solid rgba(51,51,51,0.1) !important;
	}
	.rotateEffect
	{
		transition: all 0.3s !important;
	}
	.rotateEffect:hover
	{
		transition: all 0.3s !important;
		transform: scale(1.3) rotate(10deg) !important;
	}
</style>
	


