<template>
	<div>
		<v-layout row>
		    <v-flex xs12>
		      <v-card>
		        <v-toolbar color="cyan" dark class="navbar-fixed-propia">
				  <v-btn icon v-on:click="go('/')"><v-icon class="goback" color="white" large light>keyboard_arrow_left</v-icon></v-btn>
		          <v-toolbar-title class="title">Geografía | Lautaro Umpierrez</v-toolbar-title>
		        </v-toolbar>
		        <br>
		        <v-list two-line class="items">
					<h1 class="temas-title">Temas</h1>
		          <template v-for="(item, index) in items" class="item propio"  v-on:click="go(item.url)">
		            <v-subheader v-if="item.header" :key="item.header">{{ item.header }}</v-subheader>
		            <v-divider v-else-if="item.divider" :inset="item.inset" :key="index"></v-divider>
		            <v-list-tile v-on:click="go(item.url)" avatar v-else :key="item.title">
		              <v-list-tile-avatar>
		                <img :src="item.img">
		              </v-list-tile-avatar>
		              <v-list-tile-content>
		                <v-list-tile-title v-html="item.title"></v-list-tile-title>
		                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
		              </v-list-tile-content>
		            </v-list-tile>
		          </template>
		        </v-list>
		      </v-card>
		    </v-flex>
		  </v-layout>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				items:[
					{title:'Hidrovía - Limites y fronteras',subtitle:'Oral : Hidrovía | Limites y fronteras',url: '/geografy/hidrovia-limites-y-fronteras',img:'http://webquest4esofyn.ucoz.es/orador.jpg'},
				]
			}
		},
		methods:{
			go(url)
			{
				this.$router.push(url);
			}
		},
		computed:{

		}
	}
</script>
<style lang="scss">
	.temas-title
	{
		padding-left: 10px !important;
		font-weight: 300 !important;
	}
</style>
	


