<template>
	<div>
		<div class="imagePreview" v-for="(image,index) in images" v-on:click="openVisor(image.src,image.alt,index)" :key='index'> 
			<img :width="image.preview.width" :height="image.preview.height" :src="image.src" :alt="image.alt">
			<v-icon>zoom_in</v-icon>
			<div class="pantallaHover"></div>
		</div>
		<div class="visorImage" v-bind:class="[visor.active ? 'open animated fadeIn' : '']">
			<div class="contenedor-image">
				<center>
					<img :src="visor.image"  :alt="visor.alt" class="animated rollIn">
				</center>
			</div>
			<v-btn icon class="left-arrow-visor" v-on:click="back()"><v-icon>keyboard_arrow_left</v-icon></v-btn>
			<v-btn icon class="right-arrow-visor" v-on:click="next()"><v-icon>keyboard_arrow_right</v-icon></v-btn>
			<v-btn icon class="cross"  v-on:click="closeVisor()"><v-icon>close</v-icon></v-btn>
		</div>
	</div>
</template>

<script>
	export default{
		props:['images'],
		data()
		{
			return{
				
				visor:{
					active:false,
					image:'',
					alt:'',
					index:0,
				}
			}
		},
		methods:{
			openVisor(image,alt,index)
			{
				this.visor.image=image;
				this.visor.alt=alt;
				this.visor.index=index;

				var html=document.querySelector('html');
				//Quitar scroll vertical
				html.style['overflow-y'] = 'hidden';
				this.visor.active=true;
			},
			closeVisor()
			{
				var html=document.querySelector('html');
				//Colocar scroll vertical
				html.style['overflow-y'] = 'scroll';
				this.visor.active=false;
			},
			back()
			{
				var index=this.visor.index;
				var image={};
				if(index ==0)
				{
					var cantidad = this.images.length-1;
					image=this.images[cantidad];
					this.visor.image=image.src;
					this.visor.alt=image.alt;
					this.visor.index=cantidad;
					console.log(image)
				}else{
					image=this.images[index-1];
					this.visor.image=image.src;
					this.visor.alt=image.alt;
					this.visor.index=index-1;
					console.log(image);
				}
			},
			next()
			{
				var index=this.visor.index;
				var image={};
				var cantidad = this.images.length-1;
				if(index == cantidad)
				{
					image=this.images[0];
					this.visor.image=image.src;
					this.visor.alt=image.alt;
					this.visor.index=0;
				}else{
					image=this.images[index+1];
					this.visor.image=image.src;
					this.visor.alt=image.alt;
					this.visor.index=index+1;
				}
			}

		}
	}
</script>
<style lang="scss" scoped>
	.imagePreview
	{
		width: 200px !important;
		height: 125px !important;
		margin-left: 10px !important;
		display: inline-block;
		cursor: pointer !important;
		padding: 0 !important;
		.pantallaHover
		{
			transition: all 0.3s !important;
			height: 127px !important;
			width: 200px !important;
			position: absolute !important;
			margin-top: -132px !important;
			border-radius: 6px !important;
		}
		i
		{
			position: absolute !important;
			font-size: 60px !important;
			margin-left: 70px !important;
			margin-top: -95px !important;
			color: #fff !important;
			font-weight: 700 !important;
			z-index: 10 !important;
			display: none;
		}
		img
		{
			border-radius: 6px !important;
		}
		&:hover
		{
			.pantallaHover
			{
				transition: all 0.3s !important;
				background-color: rgba(51,51,51,0.6) !important;
			}
			i
			{
				display: block !important;
			}
		}
	}
	.visorImage
	{
		display: none;
		position: fixed !important;
		width: 100% !important;
		height: 100% !important;
		top: 0 !important;
		left: 0 !important;
		background-color: #fff;
		z-index: 1000 !important;
		.right-arrow-visor
		{
			position: fixed !important;
			color: #333333 !important;
			right: 20px !important;
			top: calc(50% - 20px) !important;
			i
			{
				font-size: 40px !important;
			}
		}
		.left-arrow-visor
		{
			position: fixed !important;
			color: #333333 !important;
			left: 20px !important;
			top: calc(50% - 20px) !important;
			i
			{
				font-size: 40px !important;
			}
		}
		.cross
		{
			position: fixed !important;
			top: 20px !important;
			right: 30px !important;
			color: #333333 !important;
			cursor: pointer !important;
			i
			{
				font-weight: 300 !important;
			}
		}
		.contenedor-image
		{
			width: 100%;
			height: 50% !important;
			position: fixed !important;
			top: 15% !important;
			left: 0 !important;
			img
			{
				max-height: 500px !important;
				border-radius: 5px !important;
			}
		}
	}
	.visorImage.open
	{
		display: block !important;
	}
	@media screen and (max-width: 768px )
	{
		.visorImage
		{
			.contenedor-image
			{
				img
				{
					width: 98% !important;
					margin-left: 0.5% !important;
				}
			}
			.right-arrow-visor
			{
				top: calc(100% - 50px) !important;
			}
			.left-arrow-visor
			{
				top: calc(100% - 50px) !important;
			}
		}
	}

</style>


