<template>
	<v-container fluid class="single-post">
		<v-toolbar color="cyan" dark class="navbar-fixed-propia">
			<v-btn icon v-on:click="go('/blog')">
				<v-icon color="white" large>keyboard_arrow_left</v-icon>
			</v-btn>
			<v-spacer></v-spacer>
			</v-btn>
		</v-toolbar>
		<v-layout style="padding-top: 70px !important; padding-bottom: 30px !important;">
			<br>
			<v-flex xs12>
				<div class="post">
					<br>
					<center>
						<v-progress-circular v-show="post.loading" color="blue" indeterminate :size="80" ></v-progress-circular>
					</center>
					<h1 class="flat-font">{{ post.title }}</h1>
					<br>
					<p v-html="post.text">
						
					</p>
				</div>
			</v-flex>
		</v-layout>
		
	</v-container>
</template>
<script>
	import axios from 'axios';
	export default{
		mounted()
		{
			this.getPost();
		},
		data(){
			return{
				text: this.$route.params.id,
				post:{
					title:'',
					text:'',
					loading:true
				}
			}
		},
		methods:{
			go(url)
			{
				this.$router.push(url);
			},
			getPost()
			{
				var id_post=this.$route.params.id;
				axios.get('https://bloglautaro.000webhostapp.com/Cposts/findOne?id='+id_post).then((response)=>{
					this.post.title = response.data.title;
					this.post.text = response.data.text;
					this.post.loading=false;
				});
			}
		},
		computed:{

		},
		watch:{
			'$route'(to,from)
			{
				this.text = to.params.id;
			}
		}
	}
</script>
<style lang="scss">
	.navbar-fixed-propia
	{
		position: fixed !important;
		top: 0 !important;
		z-index: 2 !important;
	}
	.flat-font
	{
		font-weight: 300 !important;
	}
	.post{
		h1
		{
			text-align: center !important;
		}
		p
		{
			text-align: justify !important;
			width: 90% !important;
			margin-left: 5% !important;
			word-wrap: break-word !important;
		}
	}
	body{
		background-color: #fff !important;
	}
	.container.fluid.single-post
	{
		padding: 0 !important;
		background-color: #fff !important;
	}
</style>
<style scoped>

</style>



