<template>
	<div>
		<v-layout>
		    <v-flex xs12>
		      <v-card>
		        <v-toolbar color="cyan" dark class="navbar-fixed-propia">
		          <v-toolbar-title class="title">Trabajos Lautaro Umpierrez</v-toolbar-title>
		          <v-spacer></v-spacer>
		          <v-btn icon v-bind:class="[sheet ? 'activebtnmy' : '']" v-on:click="sheet=!sheet">
		          	<v-icon>more_vert</v-icon>
		          </v-btn>
		        </v-toolbar>
		        <v-list two-line class="items">
					<input v-model="inputSearch" placeholder="Busque las material" type="text" class="search">
					<template v-for="(item, index) in getSubject" class="item propio" v-on:click="go(item.url)">
			            <v-subheader v-if="item.header" :key="item.header">{{ item.header }}</v-subheader>
			            <v-divider v-else-if="item.divider" :inset="item.inset" :key="index"></v-divider>
			            <v-list-tile v-on:click="go(item.url)" avatar v-else :key="item.title">
			              <v-list-tile-avatar>
			                <img :src="item.img">
			              </v-list-tile-avatar>
			              <v-list-tile-content>
			                <v-list-tile-title v-html="item.title"></v-list-tile-title>
			                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
			              </v-list-tile-content>
			            </v-list-tile>
			          </template>
			        </v-list>
		      </v-card>
		    </v-flex>
		  </v-layout>
		  <transition name="fade">
		  	<div class="closesheet" v-bind:class="[sheet ? 'active' : '']"></div>
		  </transition>
	      <v-container fluid>
		   	<div class="text-xs-center">
			   <v-bottom-sheet v-model="sheet">
			      <v-list>
			        <v-subheader>Opciones</v-subheader>
			        <v-list-tile @click="go('/developer')">
			          <v-list-tile-avatar>
			            <v-avatar size="32px" tile>
			              <img src="../assets/user.png" height="512" width="512" alt="tile.title">
			            </v-avatar>
			          </v-list-tile-avatar>
			          <v-list-tile-title>Desarrollador</v-list-tile-title>
			        </v-list-tile>
			        <v-list-tile @click="[sheet = false,go('/blog')]">
			          <v-list-tile-avatar>
			            <v-avatar size="32px" tile>
			              <img src="../assets/new-file.png" height="512" width="512" alt="tile.title">
			            </v-avatar>
			          </v-list-tile-avatar>
			          <v-list-tile-title>Blog</v-list-tile-title>
			        </v-list-tile>
			      </v-list>
				</v-bottom-sheet>
				 <v-dialog v-model="dialogOfMe" class="mydialog" max-width="700px">
			        <v-card>
			          <v-card-title>
			            <span><h3 class="flat-font">Lautaro Umpierrez</h3></span>
			            <v-spacer></v-spacer>
			          </v-card-title>
			          <p class="padding-5">
			          	Soy un desarrollador web, mobile y desktop en diferentes lenguajes de programación, centrandome mayormente en el <strong>JS STACK</strong>.
			          	Tengo 14 años y dedico todo el tiempo que me sobra después de la secundaria a aprender y programar.
						<br>Los Lenguajes que manejo son: <strong>Javascript, Html5, Css3 Php7/Php5.5+</strong>
						<br>Y los frameworks que utilizo son: <strong>VueJS, SailsJS,NodeJS,ElectronJS,CodeIgniter y Cordova</strong>
			          </p>
			          <v-card-actions>
			            <v-btn color="primary" flat @click.stop="dialogOfMe=false">Cerrar</v-btn>
			          </v-card-actions>
			        </v-card>
			      </v-dialog>
			</div>
		</v-container>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				sheet:false,
				inputSearch:'',
				dialogOfMe:false,
				items:[
					{title: 'Biología',subtitle:'Trabajos de biologia.',url:'/biology',img:'http://www.basicknowledge101.com/photos/2016/biology_sketches.jpg'},
					{title: 'Historia',subtitle:'Trabajos de historia.',url:'/history',img:'https://png.pngtree.com/element_origin_min_pic/16/08/23/1457bbecacd784c.jpg'},
					{title: 'Cívica',subtitle:'Trabajos de cívica',url:'/civica',img:'https://tutorias.cenfomar.com/wp-content/uploads/2017/06/civica.png'},
					{title: 'Idioma español',subtitle:'Trabajos de Idioma español',url:'/idespanol',img:'https://2.bp.blogspot.com/-gu7o4rBBYsE/Vb79luL-UQI/AAAAAAAAnGU/O8oFV1MEaMw/s1600/pizarra.jpg'},
					{title: 'Geografía',subtitle:'Trabajos de geografía',url:'/geografy',img:'https://compartirpalabramaestra.org/sites/default/files/las-tic-en-la-ensenanza-de-la-geografia.jpg'}
				],

			}
		},
		methods:{
			go(url)
			{
				this.$router.push(url);
			}
		},
		computed:{
			getSubject()
			{
				return this.items.filter((subject) => subject.title.toLowerCase().includes(this.inputSearch.toLowerCase()))
			}
		}
	}
</script>
<style lang="scss">
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .5s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
	  opacity: 0;
	}
	.title{
		color: #fff !important;
	}
	body
	{
		padding-bottom: 0px !important;
	}
	.items
	{

		padding-top: 70px !important;
	}
	.search{
		transition: all 0.3s !important;
		padding: 5px !important;
		height: 35px !important;
		width: 100% !important;
		border-bottom: 1px solid rgba(51,51,51,0.1) !important;
		&:focus
		{
			transition: all 0.3s !important;
			outline: 0px !important;
			border-bottom: 2px solid #3498db !important;
		}
	}
	.flat-font
	{
		font-weight: 300 !important;
	}
	.activebtnmy
	{
		background-color: rgba(255,255,255,0.4) !important;
	}
	.closesheet
	{
		width: 100% !important;
		height: 100% !important;
		position: fixed !important;
		background-color: rgba(51,51,51,0.5) !important;
		display: none;
		top: 0 !important;
	}
	.closesheet.active
	{
		display: block !important;
	}
	.dialog.bottom-sheet
	{
		background-color: #fff !important;
	}
	.padding-5
	{
		width: 96% !important;
		margin-left: 2% !important;
		text-align: left !important;
	}
	.mydialog
	{
		.dialog
		{
			background-color: #fff !important;
		}
	}
</style>

			


