<template>
	<div>
    <v-tabs v-model="active" color="info" class="mistabs" slider-color="white" fixed-tabs>
      <!--POSTS LIST TAB-->
      <v-tab :key="1" light ripple class="tab-propias">
        Listado de posts
      </v-tab>
      <!--Create TAB-->
      <v-tab :key="2" light ripple class="tab-propias">
        CREATE POST
      </v-tab>
      <!--CONTENTS-->
      <!--POSTS LIST TabContent-->
      <v-tab-item :key="1" class="tab-content-propia">
        <v-card>
          <v-card-text>
          	<v-list two-line>
	          <template v-for="(item, index) in posts">
	            <v-list-tile avatar :key="item.id" @click="">
	              <v-list-tile-avatar>
	                <img :src="item.portada">
	              </v-list-tile-avatar>
	              <v-list-tile-content>
	                <v-list-tile-title v-html="item.title"></v-list-tile-title>
	                <v-list-tile-sub-title v-html="item.description"></v-list-tile-sub-title>
	              </v-list-tile-content>
	              <v-list-tile-action row>
		              <v-btn icon ripple row v-on:click="go('/blog/Administration/editPost/'+item.id)">
		                <v-icon color="info lighten-1">edit</v-icon>
		              </v-btn>
		              <v-btn icon ripple row v-on:click="deletePost(item.id)">
		                <v-icon color="info lighten-1">delete</v-icon>
		              </v-btn>
		          </v-list-tile-action>
	            </v-list-tile>
	          </template>
	        </v-list>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <!--Create POST TabContent-->
      <v-tab-item :key="2" class="tab-content-propia">
        <v-card flat>
          <v-card-text>
          	<v-text-field placeholder="Set the title of your post" v-model="create.title"></v-text-field>
          	<v-text-field placeholder="Set the principal image of your post" v-model="create.portada"></v-text-field>
          	 <v-select :items="create.categories" v-model="create.selectedCategory" label="Select" single-line></v-select>
          	 <v-text-field multi-line :rules="[(v) => v.length <= 200 || 'Max 200 characters']" label="Description text." v-model="create.description" :counter="200"></v-text-field>
			<wysiwyg v-model="create.content"></wysiwyg>
			<center>
				<v-btn color="info" v-on:click="createPost()">
					Crear Post				
				</v-btn>
				<v-btn v-on:click="dialog.active=true">
	          		Agregar una imagen (link)
	          	</v-btn>
	        </center>
          </v-card-text>
        </v-card>
      </v-tab-item>	
    </v-tabs>
     <v-btn v-on:click="desconect" outline medium fab color="error" class="desconect">
      <v-icon>exit_to_app</v-icon>
     </v-btn>

     <v-dialog v-model="dialog.active" max-width="500">
      <v-card>
        <v-card-title class="headline">Add Image</v-card-title>
        <v-card-text>
        	<v-text-field placeholder="Set link of your image" v-model="dialog.link"></v-text-field>
        	<v-text-field placeholder="Set width of your image" v-model="dialog.width"></v-text-field>
        	<v-text-field placeholder="Set height of your image (optional)" v-model="dialog.height"></v-text-field>
        	<v-text-field placeholder="Set description of your image" v-model="dialog.alt"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" v-on:click="addImage()">Crear imagen</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </div>

</template>
<script>
	import axios from 'axios';
	export default{
		mounted()
		{
			this.verifySession();
			this.getPosts();
		},
		data(){
			return{
				token:window.localStorage.getItem('token'),
				text:'dskadjkasljdklasj daskl jdlaskdjaslkdsjakl j',
				active:0,
				create:{
					content:'hellow',
					title:'',
					portada:'',
					description:'',
					categories:[
						'LGBTQ',
						'PROGRAMACION'
					],
					selectedCategory:''
				},
				posts:[],
				dialog:{
					active:false,
					link:'',
					width:'',
					height:'',
					alt:''
				}
			}
		},
		methods:{
			verifySession()
			{
				if(this.token && this.token != '')
				{
					axios.get('https://bloglautaro.000webhostapp.com/Cadmin?token='+this.token).then((response)=>{
						if(response.status==500)
						{
							alert("Debes iniciar sesión para estár aquí....");
							this.go('/blog/login');
							window.localStorage.setItem('token','');
						}else{
							if(response.data.error)
							{
								alert("Debes iniciar sesión para estár aquí....");
								this.go('/blog/login');
								window.localStorage.setItem('token','');
							}
						}
					});
				}else{
					this.go('/blog/login');
				}
			},
			go(url)
			{
				this.$router.push(url);
			},
			desconect()
			{
				this.token='';
				window.localStorage.setItem('token','');
				this.go('/blog/login');
			},
			addImage()
			{
				if(this.dialog.link != '' && this.dialog.width != '')
				{
					if(this.dialog.height == '')
					{
						this.create.content += `<img src="${this.dialog.link}" width="${this.dialog.width} alt="${this.dialog.alt}">`;
					}else{
						this.create.content += `<img src="${this.dialog.link}" width="${this.dialog.width} height="${this.dialog.height}" alt="${this.dialog.alt}">`;
					}
				}
				this.dialog.link='';
				this.dialog.width='';
				this.dialog.height='';
				this.dialog.alt='';
				this.dialog.active=false;
			},
			getPosts()
			{
				axios.get('https://bloglautaro.000webhostapp.com/Cposts').then((response)=>{
					if(response.status == 500)
					{
						this.desconect();
					}else{
						this.posts=response.data;
					}
				})
			},
			deletePost(id)
			{
				var confirmacion = confirm('¿ Estás seguro que deseas eliminar esta entrada ?');
				if(confirmacion)
				{
					axios.get(`https://bloglautaro.000webhostapp.com/Cadmin/delete?token=${this.token}&post_id=${id}`).then((response)=>{
						alert(response.data.results);
						this.getPosts();
					});
				}
			},
			createPost()
			{
				if(this.create.title.length > 5 && this.create.content != '' && this.create.selectedCategory!='' && this.create.portada.length > 5 && this.create.description != '')
				{
					var title=this.create.title;
					var text = this.create.content;
					var portada = this.create.portada;
					var category = this.create.selectedCategory.toLowerCase();
					var description = this.create.description;
					let data = new FormData();
				    data.append('title', title);
				    data.append('text', text);
				    data.append('portada', portada);
				    data.append('category', category);
				    data.append('description', description);
					axios({
						url:`https://bloglautaro.000webhostapp.com/Cadmin/create?token=${this.token}`,
						method:'POST',
						data
					}).then((response)=>{
						alert(response.data.result);
						this.create.title="";
						this.create.content="";
						this.create.portada="";
						this.create.selectedCategory="";
						this.create.description="";
						this.getPosts();
					});
				}
			}
		},
		computed:{

		}
	}
</script>
<style lang="scss">
	.desconect
	{
		position: fixed !important;
		bottom: 4px !important;
		right: 5px !important;
	}
	.tab-propias
	{
		color: #fff !important;
	}
	.tabs.mistabs
	{
		padding-left: 0px !important;
		padding-right: 0px !important;
		.tab-content-propia
		{
			padding-left: 0px !important;
			padding-right: 0px !important;
			width: 100% !important;
		}
		.tabs__container
		{
			padding-bottom: 2px !important;
			.tabs__slider-wrapper
			{
				margin-top: -2px !important;
			}
		}
	}
</style>


