import Home from './components/Home.vue';
import Developer from './components/Developer.vue';
//Biología
import Biology from './components/biology/Home.vue';
import CoastalEcosystem from './components/biology/Coastalecosystem.vue';
//History
import History from './components/history/Home.vue';
import LGBTCommunity from './components/history/Lgbtcommunity.vue';
//Civica
import Civica from './components/civica/Home.vue';
import FredomOfExpression from './components/civica/Freedomofexpressionright.vue';
// Idioma español
import IdEspanol from './components/idespanol/Home.vue';
import Entrevista1 from './components/idespanol/entrevista-1.vue';
// Geografía
import Geografy from './components/geografy/Home.vue';
import GeografyHidroviaLF from './components/geografy/hidrovia-limites-y-fronteras.vue';
//Blog
import BlogHome from './components/blog/Home.vue';
import BlogPost from './components/blog/Post.vue';
import BlogLogin from './components/blog/Login.vue';
import BlogAdministration from './components/blog/Administration.vue';
import BlogEditPost from './components/blog/Editpost.vue';

export const routes=[
	{ path:'/',component:Home,name:'home'},
	{ path:'/developer',component:Developer,name:'developer'},
	{ path:'/biology',component:Biology,name:'biology'},
		//Rutas dentro de /biology.
		{ path:'/biology/ecosistema-costero',component:CoastalEcosystem , name:'CoastalEcosystem'},
	{path:'/history',component:History, name:'history'},
		//Rutas dentro de /history
		{path: '/history/lgbtq-community',component:LGBTCommunity,name:'LGBTCommunity'},
	{path:'/blog', component: BlogHome, name:'blog'},
		//Rutas dentro de /blog
		{ path: '/blog/post/:id', component:BlogPost,name:'blogPost'},
		{ path: '/blog/Login', component:BlogLogin,name:'blogLogin'},
		{path:'/blog/administration', component: BlogAdministration, name: 'blogAdministration'},
		{path:'/blog/administration/editPost/:id', component: BlogEditPost, name: 'blogAdministrationEditPost'},
	{path: '/civica', component: Civica, name:'civicaHome'},
		//Rutas dentro de /civica
		{path:'/civica/Freedom-of-expression',component:FredomOfExpression},
	{path:'/idespanol',component:IdEspanol, name:'idespañolHome'},
		//Rutas dentro de /idespanol
		{path:'/idespanol/entrevista-1',component:Entrevista1},
	{path:'/geografy',component: Geografy, name:'geografía'},
		//Rutas dentro de /geografy
		{path:'/geografy/hidrovia-limites-y-fronteras', component: GeografyHidroviaLF}
];


